.categories {
    color: var(--turqoise);

}

.categories .categories-header {
    text-align: left;
    margin: 20px 0 0 10%;
    color: var(--turqoise);
}

.categories .categories-breakline {
    width: 80%;
    height: 2px;
    border-width: 0;
    color: #3c3c3c;
    background: #3c3c3c;
}

.categories .categories-browser {
    display: flex;
    flex-direction: row;
    margin: 0 10%;
}

.categories .categories-image {
    height: 200px;
    width: 150px;
    margin: 20px 40px 0 0;
}