/* Popup style */
.popup-box {
    position: fixed;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
  }
   
.box {
    position: relative;
    justify-content: center;
    align-items: center;
    max-width: 30%;
    margin: 0 auto;
    height: 20%;
    max-height: 70vh;
    margin-top: calc(110vh - 85vh - 30px);
    background: gray;
    border-radius: 4px;
    padding: 30px;
    border: 1px solid #999;
    overflow: auto;
}

.close-icon {
  content: 'X';
  cursor: pointer;
  color: var(--almostBlack);
  position: fixed;
  right: calc(35% - 10px);
  top: calc(110vh - 85vh - 35px);
  background: var(--turqoise);
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1px solid #999;
  font-size: 20px;
}

@media (max-width: 700px) {
  .box {
    max-width: 80%;
  }
  .close-icon {
    right: calc(10% - 5px);
  }
}

   
