.watchlist {
    background-color: var(--almostBlack);
    color: var(--turqoise);
    padding: 1em;
    box-sizing: border-box;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    height: fit-content;
    
}
.watchlist-scrollerItem {
    grid-column: 1fr;
    padding: 10px;
}
.watchlist-scrollerImg {
    width: 60%;
    height: 300px;
    object-fit: cover;
    justify-self: center;
}

@media (max-width: 800px) {
    .watchlist {
        grid-template-columns: repeat(2, 1fr);
    }
  }
  
@media (max-width: 500px) {
    .watchlist-scrollerItem {
        grid-template-columns: 1fr;
    }
    .watchlist-scrollerImg {
        width: 100%;
        height: 170px;
        object-fit: contain;
    }
}